$primary-color: #cca53b;
$secondary-color: gainsboro;
// $dark-color: #001625;
$dark-color: #000d25;
$error-color: #d67c53;
$font-sans: 'Montserrat', sans-serif;
$font-serif: 'Abril Fatface', serif;
$font-big: 60px;
$font-medium: 30px;
$font-small: 20px;
$font-p: 15px;
$font-tiny: 12px;
$font-heavy: 700;
$font-light: 400;

body {
  font-family: $font-sans;
  font-weight: $font-light;
  font-size: $font-small;
  line-height: 1.6;
  background-color: $dark-color;
  color: $secondary-color;
}

h1 {
  font-family: $font-serif;
  font-size:  $font-big !important;
}
  
h2 {
  font-family: $font-serif;
  font-size: $font-medium !important;
}
  
h3 {
  font-family: $font-sans;
  font-size:  $font-small !important; 
  color: $primary-color;
}

h4 {
    font-family: $font-sans;
    font-size:  $font-small !important; 
    color: $primary-color;
}

h5 {
    font-family: $font-sans;
    font-size:  $font-small !important; 
    color: $primary-color;
}
  
p {
  font-family: $font-sans;
  font-size:  $font-p !important; 
}

.main-view{
  padding: 20px;
}

.page-header {
  display: flex;
  width: 100%;
  align-items: center; 
  color: $primary-color;
  justify-content: flex-end;
}

.page-header__item {
  background-color: $primary-color;
  color: $primary-color;
  // margin-left: 1rem;
}

.page-header__item:last-child {
  background-color: $primary-color;
  color: $dark-color;
  font-weight: $font-heavy;
}

.logo{
  font-family: $font-serif;
  font-size: $font-big;
  line-height: 1.6;
  color: $primary-color;
}

.logo:hover {
  color: $primary-color;
}

