.btn-primary:active,
.btn-primary:hover,
.btn-primary:focus {
    background-color: $primary-color !important;
      border: 1px solid $primary-color !important;
      border-radius: 0;
      color: $dark-color !important;
      font-weight: $font-heavy;
  }
  
.btn-primary {
  background-color: $dark-color !important;
  color: $primary-color !important;
  border: 1px solid $primary-color !important;
  box-shadow: none !important;
}


.btn-full {
  background-color: $primary-color !important;
  border: 1px solid $primary-color !important;
  border-radius: 0;
  color: $dark-color !important;
  font-weight: $font-heavy;
}

.btn-logout {
  background-color: transparent !important;
  border: 1px solid $primary-color;
  border-radius: 0;
  color: $primary-color !important;
}

.btn-logout:active,
.btn-logout:hover,
.btn-logout:focus {
  background-color: $primary-color !important;
  color: $dark-color !important;
  border: 1px solid $primary-color !important;
  box-shadow: none !important;
}

.btn-update {
  background-color: $primary-color !important;
  color: $dark-color !important;
  border: 1px solid $primary-color !important;
  border-radius: 0;
  box-shadow: none !important;
  font-weight: $font-heavy;
}

.navbar {
  padding: 0  !important;
  margin: 0  !important;
}

.nav-link {
  padding: 0 !important;
  padding-left: 1rem !important;
}

.round {
  color: $dark-color;
  height: 4rem;
  border-radius: 50% !important;
  font-weight: $font-heavy;
  width: 4rem;
}

.btn-profile {
  // background-color: transparent !important;
  border: 1px solid $primary-color;
  border-radius: 0;
  color: $dark-color !important;
  // padding:  0.75rem;
}

.btn-profile:active,
.btn-profile:hover,
.btn-profile:focus {
  background-color: $secondary-color !important;
  border: 1px solid $primary-color !important;
  box-shadow: none !important;
}

.error{
  color:$error-color;
}

.form-info{
  font-size: $font-tiny;
}

.btn-link{
  background-color: transparent !important;
  border: 1px solid $primary-color;
  border-radius: 0;
  color: $primary-color !important;
  font-weight: $font-heavy;
}